  calculadoSituacionDeLasInstalaciones() {
    let situacionDeLasInstalaciones =
      this.registroNuevoFormularioForm.get("precioDeInventario").value -
      this.registroNuevoFormularioForm.get("precioDeCompra").value;
    if (situacionDeLasInstalaciones) {
      this.registroNuevoFormularioForm
        .get("situacionDeLasInstalaciones")
        .setValue(situacionDeLasInstalaciones);
    }
  }
  calculadoMarcasComerciales() {
    let marcasComerciales =
      this.registroNuevoFormularioForm.get("precioDeInventario").value /
      this.registroNuevoFormularioForm.get("precioDeCompra").value;
    if (marcasComerciales) {
      this.registroNuevoFormularioForm
        .get("marcasComerciales")
        .setValue(marcasComerciales);
    }
  }
  calculadoPrincipalesProductos() {
    let principalesProductos =
      this.registroNuevoFormularioForm.get("precioDeInventario").value -
      this.registroNuevoFormularioForm.get("precioDeCompra").value -
      this.registroNuevoFormularioForm.get("calcTotalDescuentos").value;
    if (principalesProductos) {
      this.registroNuevoFormularioForm
        .get("principalesProductos")
        .setValue(principalesProductos);
    }
  }
  calculadoPrincipalesCompetidores() {
    let principalesCompetidores =
      (this.registroNuevoFormularioForm.get("utilidadCalculada").value *
        this.registroNuevoFormularioForm.get("precioDeCompra").value) /
      this.registroNuevoFormularioForm.get("precioDeInventario").value;
    if (principalesCompetidores) {
      this.registroNuevoFormularioForm
        .get("principalesCompetidores")
        .setValue(principalesCompetidores);
    }
  }
  calculadoNombreCompleto() {
    let nombreCompleto =
      this.registroNuevoFormularioForm.get("nombre").value +
      " " +
      this.registroNuevoFormularioForm.get("apellidoPaterno").value +
      " " +
      this.registroNuevoFormularioForm.get("apellidoMaterno").value;
    if (nombreCompleto) {
      this.registroNuevoFormularioForm
        .get("nombreCompleto")
        .setValue(nombreCompleto);
    }
  }

