                          instrumentoMonetario:string, 
                          montoPromedioDeDepositosARealizar:number, 
                          numeroDeTransacciones:number, 
                          enQueOcasiones:string, 
                          tipoDeGarantia:string, 
                          cualOtraActividadQueGenereIngresos:string, 
                          datosDelRepresentanteLegal:SetDatosBasicosUnaDireccion[], 
                          datosDeNacionalidad:SetDatosNacionalidad[], 
                          rfcConHomoclaveRepresentanteLegal:string, 
                          curpRepresentanteLegal:string, 
                          numeroSerieFirmaElectronicaRL:string, 
                          datosNotarialesRepresentanteLegal:SetDatosNotariales[], 
                          calidadMigratoria:string, 
                          ocupacion:string, 
                          profesion:string, 
                          caracterConElQueRepresenta:string, 
                          actividadOGiroDeNegocio:string, 
                          dependenciaPuestoPeriodo:SetDependenciaPuestoPeriodo[], 
                          estructuraAccionariaPersonasFisicas:SetEstructuraAccionariaPersonasFisicas[], 
