                          principalesProveedoresPF:SetDatosProveedorPrincipalPersonaFisica[], 
                          principalesProveedoresPM:SetDatosProveedorPrincipalPersonaMoral[], 
                          principalesClientesPF:SetDatosClientesPersonaFisica[], 
