import { NgModule,LOCALE_ID } from "@angular/core";
import { CommonModule } from "@angular/common";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { NgbActiveModal, NgbModule, NgbTooltipConfig, NgbTooltipModule } from "@ng-bootstrap/ng-bootstrap";
import { PipesModule } from "../pipes/pipes.module";
import { RouterModule } from "@angular/router";
import localeEs from "@angular/common/locales/es";
import localeDeExtra from "@angular/common/locales/extra/de";
import { registerLocaleData } from "@angular/common";
import { MaterialModule } from "../app.module";
import { NgxDatatableModule } from "@swimlane/ngx-datatable";
import { MatTooltipModule } from "@angular/material/tooltip";
import { MatButtonModule } from "@angular/material/button";
import { BsDropdownModule } from "ngx-bootstrap/dropdown";
import { NumberFormatDirective } from "./numberFormatDirective.directive";
registerLocaleData(localeEs, "es", localeDeExtra);

@NgModule({
  declarations: [ 
 NumberFormatDirective
  ],
  exports: [
    NumberFormatDirective
  ],
  imports: [
    CommonModule,
    FormsModule,
    NgbModule,
    MaterialModule,
    PipesModule,
    RouterModule,
    ReactiveFormsModule,
    NgxDatatableModule,
    MatTooltipModule,
    MatButtonModule,
    BsDropdownModule.forRoot()
  ],
  providers: [
    { provide: LOCALE_ID, useValue: "es-MX" },
    NgbActiveModal,
  ]
})
export class DirectivesModule {}






