abrirComponente(modal: string) {
  switch(modal){
    case 'RegistroNuevoFormularioComponent': this.AbrirModal(RegistroNuevoFormularioComponent);break;

  }
}


AbrirModal(componente:any) {

    const modalRef: NgbModalRef= this.modalService
      .open(componente, {
        backdropClass: "light-blue-backdrop",
        backdrop: false,
        animation: true,
        size: "xl",
      });
  
      modalRef.result.then(
        (result) => {
          window.location.reload();
          
        },
        (reason) => {
          window.location.reload();
        }
      );
  }
  


